<template>
  <div class="ServiceList">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
    </div>
    <el-form :model="serviceForm" ref="serviceForm" :rules="inputRules" label-width="92px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('redis.redisList.clusterName')+'：'" prop="zkClusterId">
            <el-select v-model="serviceForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
              <el-option
                v-for="(item) in clusterOption"
                :key="item.id"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.serviceList.applyName')+'：'" label-width="68px">
            <el-input type="text" v-model.trim="serviceForm.appName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6" align="left">
          <el-form-item label-width="0px">
           <el-button icon="el-icon-search" type="primary" size="small" @click="search('search')">{{$t('common.search')}}</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      :data="serviceList[currentPage - 1]"
      ref="multipleTable"
      v-loading="loading"
      @cell-click="cellClick"
      style="width: 100%">
      <!-- 服务名 -->
      <el-table-column
        align="center"
        prop="serName"
        width="400"
        show-overflow-tooltip
        :label="$t('service.serviceList.serviceName')">
        <template slot-scope="scope">
          {{ scope.row.serName}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="appName"
        show-overflow-tooltip
        :label="$t('service.serviceList.applyName')"
        width="120">
        <template slot-scope="scope">
          {{ scope.row.appName | filterEmpty}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="providerCount"
        :label="$t('service.serviceList.provide')">
        <template slot-scope="props">
          <el-button type="text" size="mini" @click="handleDetail('provider',props.row)">{{props.row.providerCount}}</el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="consumerCount"
        :label="$t('service.serviceList.consumer')">
        <template slot-scope="props">
          <el-button type="text" size="mini" @click="handleDetail('consumer',props.row)">{{props.row.consumerCount}}</el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="routerCount"
        :label="$t('service.serviceList.routingRule')">
        <template slot-scope="props">
          <el-button type="text" size="mini" @click="handleDetail('routingRule',props.row)">{{props.row.routerCount}}</el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="configuratorCount"
        :label="$t('service.serviceList.tmconfig')">
        <template slot-scope="props">
          <el-button type="text" size="mini" @click="handleDetail('tmconfig',props.row)">{{props.row.configuratorCount}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="serviceTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
  export default {
    name: 'ServiceList',
    data() {
      return {
        loading: false,
        currentPage: 1, // 当前页数
        // 查询模板
        serviceForm: {
          zkClusterId: '' // 集群分组
        },
        // 查询/弹出框-规则
        inputRules: {
          zkClusterId: [
            { required: true, message: '请输入集群名称', trigger: 'blur' }
          ]
        }
      }
    },
    activated() {
      if (this.clusterOption.length <= 0) {
        const vm = this
        this.initializeData({vm})
      }
    },
    computed: {
      ...mapState({
        serviceList: state => state.service.serviceList,
        serviceTotal: state => state.service.serviceTotal,
        classify: state => state.service.classify,
        clusterOption: state => state.service.clusterOption
      })
    },
    methods: {
      ...mapActions('service', ['initializeData', 'serviceFind']),
      // 翻页事件
      handleCurrentChange(val) {
        this.currentPage = val
      },
      // 查询
      search() {
        if (!this.serviceForm.zkClusterId) {
          this.$message({
            message: '带*的值不能为空！',
            type: 'warning'
          })
          return
        }
        this.loading = true
        this.serviceFind([this, this.serviceForm])
      },
      // 单击单元格
      cellClick(row, column, cell, event) {
      },
      handleDetail(type, row) {
        let {zkClusterId, serName, appName} = row
        let query = Object.assign({}, {
          zkClusterId,
          'name': serName,
          'appliName': appName
        })
        this.$router.push({
          name: type,
          params: query
        })
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>

</style>



